* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  color: #a4acc4;
  font-family: cursive;
  font-size: 1.2rem;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #0056d898;
    border-radius: 50px;
  }

  &::-webkit-scrollbar-track {
    background-color: #777;
  }
}

.main-content {
  width: 85%;
  margin-left: 15%;
  min-height: 100vh;
  background: linear-gradient(90deg, rgba(16,18,27,1) 0%, rgba(0,28,70,1) 100%);

}

.blue {
 color: #037FFF;
}

.container {
  max-width: 1170px;
  padding: 15px 15px;
  margin: 0 auto;
}

.tsparticles-canvas-el {
  min-height: 100vh;
}