@import '../styles/title';

.main-page {
  text-align: center;

  &__header {
    color: #fff;
    font-size: 3.2rem;
  }

  &__header-inner {
    color: #037FFF;
  }

  &__icon-holder {
    font-size: 4rem;
    color: #a4acc4;
    transition: .5s;

    &:hover {
      color: #037FFF;
    }
  }

  &__wrapper {
    position: absolute;
    top: 40%;
    margin: 0 10rem;
  }

  &__header {
    max-width:715px;;
    margin: 0 auto;
  }

  &__text, &__header {
    backdrop-filter: blur(5px);
    border-radius: 20%;
  }
}